<script setup>
import memo from "../assets/memo.pdf";
import checkList from "../assets/checkList.pdf";

const downloadFiles = () => {
  const files = [
    { url: memo, name: "memo.pdf" },
    { url: checkList, name: "checkList.pdf" },
  ];

  const download = (file, delay = 0) => {
    setTimeout(() => {
      const link = document.createElement("a");
      link.href = file.url;
      link.download = file.name;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }, delay);
  };

  files.forEach((file, idx) => download(file, idx * 300));
};
</script>
<template>
  <div class="container">
    <div class="container__gift">
      <div class="container__text">
        ВЫ ОТЛИЧНО СПРАВИЛИСЬ! <br />
        НАЖМИТЕ
      </div>
      <div @click="downloadFiles" class="container__gift-link">
        <div class="container__arrow">
          <img class="arrow" src="../assets/arrow.png" alt="" />
        </div>
        <div class="container__box">
          <img class="gift" src="../assets/gift.png" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

