<script setup>
import { ref, watch, onMounted } from "vue";
import Card from "./Card.vue";
import Button from "./Button.vue";
import Popup from "./Popup.vue";
import Gift from "./Gift.vue";

const cardsArr = ref([
  {
    text: "Издать приказ о введении сменного режима работы и внести изменения в ЛНА компании",
    isCorrect: true,
    reverseSideText: "Верно. Перечень документов перечислен в статье 65 ТК РФ",
    positionNum: 0,
  },
  {
    text: "Составить график сменности: указать конкретные смены и их продолжительность",
    isCorrect: true,
    reverseSideText:
      "Верно. Работодатель может попросить дополнительные документы от работника",
    positionNum: 1,
  },
  {
    text: "Не менее чем за один месяц ознакомить работника с графиком работы под роспись",
    isCorrect: true,
    reverseSideText:
      "Верно. До подписания договора нужно ознакомить с ЛНА. Статья 68 ТК РФ",
    positionNum: 2,
  },
  {
    text: "Подписать с работником приказ и ДС к трудовому договору «Об изменении режима работы»",
    isCorrect: true,
    reverseSideText: "Верно. Прием на работу оформляется трудовым договором",
    positionNum: 3,
  },
  {
    text: "Не менее чем за две недели ознакомить работника с графиком работы под роспись",
    isCorrect: false,
    reverseSideText:
      "Постановлением Правительства РФ от 24.07.2021 № 1250 обязанность вести форму Т-2 отменена",
    positionNum: -1,
  },
  {
    text: "Издать приказ о переводе на сменный график работы только для сотрудника",
    isCorrect: true,
    reverseSideText:
      " Верно. Внести запись о приеме на работу в трудовую книжку или сведения о трудовой деятельности",
    positionNum: -1,
  },
]);

const zones = ref([
  { id: "zone0", cards: [] },
  { id: "zone1", cards: [] },
  { id: "zone2", cards: [] },
  { id: "zone3", cards: [] },
  { id: "zone4", cards: [] },
  { id: "zone5", cards: [] },
]);

const activeZone = ref(null);

const onDragEnter = (zoneId) => {
  activeZone.value = zoneId;
};

const onDragLeave = () => {
  activeZone.value = null;
};

const isShowCheckBtn = ref(false);

const onDrop = (zoneId) => {
  isShowCheckBtn.value = true;
  if (isChecked.value) return;
  const droppedCardText = event.dataTransfer.getData("text");
  const cardIndex = cardsArr.value.findIndex(
    (card) => card.text === droppedCardText
  );

  if (cardIndex !== -1) {
    const [droppedCard] = cardsArr.value.splice(cardIndex, 1);
    const targetZone = zones.value.find((zone) => zone.id === zoneId);

    // Проверяем, пустая ли зона
    if (targetZone && targetZone.cards.length === 0) {
      targetZone.cards.push(droppedCard);
    } else {
      cardsArr.value.push(droppedCard);
    }
  }

  // проверка порядка добавления карточек
  // checkCards();
};

// проверка порядка добавления карточек
function checkCards() {
  activeZone.value = null;
  zones.value.forEach((zone, index) => {
    zone.cards.forEach((card) => {
      if (index === card.positionNum) {
        //   console.log(true);
        if (index === 3) {
          // console.log("its the end");
          isShowGift.value = true;
        }
      } else {
        isShowPopup.value = true;
        isCorrect.value = false;
      }
    });
  });
}

const isChecked = ref(false);
const isShowPopup = ref(false);
const isCorrect = ref(null);
const isShowGift = ref(false);

function shuffle(array) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
}

onMounted(() => {
  shuffle(cardsArr.value);
});

function restart() {
  // Сброс карточек
  cardsArr.value = [
    {
      text: "Издать приказ о введении сменного режима работы и внести изменения в ЛНА компании",
      isCorrect: true,
      reverseSideText:
        "Верно. Перечень документов перечислен в статье 65 ТК РФ",
      positionNum: 0,
    },
    {
      text: "Составить график сменности: указать конкретные смены и их продолжительность",
      isCorrect: true,
      reverseSideText:
        "Верно. Работодатель может попросить дополнительные документы от работника",
      positionNum: 1,
    },
    {
      text: "Не менее чем за один месяц ознакомить работника с графиком работы под роспись",
      isCorrect: true,
      reverseSideText:
        "Верно. До подписания договора нужно ознакомить с ЛНА. Статья 68 ТК РФ",
      positionNum: 2,
    },
    {
      text: "Подписать с работником приказ и ДС к трудовому договору «Об изменении режима работы»",
      isCorrect: true,
      reverseSideText: "Верно. Прием на работу оформляется трудовым договором",
      positionNum: 3,
    },
    {
      text: "Не менее чем за две недели ознакомить работника с графиком работы под роспись",
      isCorrect: false,
      reverseSideText:
        "Постановлением Правительства РФ от 24.07.2021 № 1250 обязанность вести форму Т-2 отменена",
      positionNum: -1,
    },
    {
      text: "Издать приказ о переводе на сменный график работы только для сотрудника",
      isCorrect: true,
      reverseSideText:
        " Верно. Внести запись о приеме на работу в трудовую книжку или сведения о трудовой деятельности",
      positionNum: -1,
    },
  ];
  // Сброс зон
  zones.value = [
    { id: "zone0", cards: [] },
    { id: "zone1", cards: [] },
    { id: "zone2", cards: [] },
    { id: "zone3", cards: [] },
    { id: "zone4", cards: [] },
    { id: "zone5", cards: [] },
  ];
  // Сброс флагов
  activeZone.value = null;
  isShowCheckBtn.value = false;
  isChecked.value = false;
  isShowPopup.value = false;
  isCorrect.value = null;
  isShowGift.value = false;
  // Перемешать карточки
  shuffle(cardsArr.value);
}
</script>

<template>
  <div class="container">
    <Transition mode="out-in">
      <div v-if="isShowPopup">
        <Popup @restart="restart" :isCorrect="isCorrect" />
      </div>
    </Transition>

    <div class="container__img">
      <img
        src="../assets/board.png
	  "
        alt=""
      />
      <div
        v-for="zone in zones"
        :key="zone.id"
        class="drop-zone"
        :id="zone.id"
        :class="{ 'drop-zone--active': activeZone === zone.id }"
        @dragover.prevent
        @dragenter="onDragEnter(zone.id)"
        @dragleave="onDragLeave"
        @drop="onDrop(zone.id)"
      >
        <div v-for="card in zone.cards" :key="card.text" class="dropped-card">
          <div class="pointer">
            <img src="../assets/kanzelyarskaya_knopka.png" alt="" />
          </div>
          <Card
            min-height="30vw"
            :text="card.text"
            :reverse-side-text="card.reverseSideText"
            :isCorrect="card.isCorrect"
            :isChecked="isChecked"
          />
        </div>
      </div>
    </div>

    <Transition mode="out-in">
      <div v-if="!isShowGift" class="container__cards">
        <Card
          v-for="item in cardsArr"
          :key="item.text"
          :text="item.text"
          :reverse-side-text="item.reverseSideText"
        />
      </div>
      <div v-else>
        <Gift />
      </div>
    </Transition>

    <Transition mode="out-in">
      <div
        v-if="!isChecked && isShowCheckBtn && !isShowGift"
        class="container__btn"
      >
        <Button @click="checkCards">ПРОВЕРИТЬ</Button>
      </div>
    </Transition>
  </div>
</template>

